
.vue-command {
  $seashell: #f1f1f1;

  @mixin clearfix() {

    &:before,
    &:after {
      content: " ";
      display: table;
    }

    &:after {
      clear: both;
    }
  }

  overflow-y: hidden;
  overflow-x: hidden;

  .vue-command__actions {
    @include clearfix();
    position: inherit;
    padding: 10px;
    background-color: #111316;
  }

  .vue-command__actions__button {
    display: inline-block;
    border-radius: 100%;

    &:before {
      content: ' ';
      display: block;
      height: 13px;
      width: 13px;
    }

    &:not(:last-child) {
      margin-right: 7px;
    }
  }

  .vue-command__actions__button--close {
    background-color: #ff5f58;
  }

  .vue-command__actions__button--minimize {
    background-color: #ffbd2e;
  }

  .vue-command__actions__button--fullscreen {
    background-color: #29ca41;
  }

  .vue-command__history {
    overflow: auto;
    background-color: #111316;
    display: block;
    padding: 0 12px 10px;
    margin: 0;
    white-space: pre-line;
    line-height: 1.33;
    color: $seashell;
    font-size: 1rem;
    font-family: monospace;
    color: #ffffff;
    height: 100%;

    input,
    textarea {
      background: none;
      border: none;
      outline: none;
      flex: 1;
      width: 100%;
      font-size: 1rem;
      resize: none;
      overflow: hidden;
      color: #ffffff;

      &::placeholder {
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }

  .vue-command__history__entry--fullscreen {
    height: 100%;
  }
}
