
.vue-command {
  .vue-command__query {
    display: flex;
  }

  .vue-command__query__input {
    background: none;
    border: none;
    outline: none;
    flex: 1;
    width: 100%;
    font-family: monospace;
    font-size: 1rem;

    &::placeholder {
      color: rgba(255, 255, 255, 0.5);
    }
  }

  .vue-command__query__prompt {
    margin-right: 0.25rem;
  }
}
